@import 'src/Style/MainVar.sass';

.container {
  display: flex;
  justify-content: center;
  margin: 48px 0;
  height: 1250px;
  transition-timing-function: ease-in-out; }

.bubblesContainer {
  position: relative;
  display: flex;
  justify-content: center;
  width: 1024px;
  > * {
    position: absolute; } }

.title {
  height: 1250px;
  text-align: center;
  position: relative;
  font-family: 'Gilroy BlackItalic';
  font-size: 40pt;
  color: $slider-title-color;
  writing-mode: vertical-rl; }

.workedOn, .myWorks {
  font-family: 'Gilroy BlackItalic';
  position: absolute;
  font-size: 16pt;
  right: 30px; }

.workedOn {
  color: #BAE5D6;
  margin-bottom: 30px;
  top: 100px;
  transform: rotate(45deg); }

.myWorks {
  color: #F8C9D5;
  bottom: 100px;
  transform: rotate(-45deg); }

.bubblesLegend {
  position: absolute; }

@media screen and (max-width: 1024px) {
  .title {
    height: auto;
    font-size: 36pt;
    writing-mode: horizontal-tb;
    margin: -100px 0 0 0; }

  .bubblesContainer {
    margin: 50px 0 0 0; }

  .container {
    height: 2250px;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start; }

  .workedOn {
    top: 60px;
    margin: 0 120px 0 0;
    right: 50%;
    transform: rotate(-45deg) translate(50%); }

  .myWorks {
    top: 2100px;
    margin: 0 100px 0 0;
    right: 50%;
    transform: rotate(45deg) translate(50%); } }
